import { Layout } from "antd";
import { Outlet, useLocation } from "react-router-dom";
import Header from "./Comps/Header";
const Pages = () => {
    return (
        <div className="home-page">
            <Layout>
                <Layout.Header className="top-head">
                    <Header />
                </Layout.Header>
                <Layout.Content>
                    <Outlet />
                </Layout.Content>
            </Layout>
        </div>
    );

}

export default Pages;